import React, { Component } from 'react';
import Select from 'react-select';
import { Container, Card, Table, Tooltip, OverlayTrigger, Button, Form } from 'react-bootstrap'
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper'
import Productlist from './Components/Productlist'
import AddProductModal from './Components/AddProductModal'
import { SyncProducts } from './Components/SyncProducts'
import SyncProductModal from './Components/SyncProductModal'
import ManualUploadModal from './Components/ManualUploadModal'
import ImportProducts from './Components/ImportProducts'
import { ACTION_PRODUCT_ADD, ACTION_PRODUCT_DELETE, fetchProductList, ACTION_PRODUCT_UPDATE, shopifyProducts, STORE_DELETE, SHOPIFY_PRODUCT_SYNC } from '../../../../actions/productAction'
import { connect } from 'react-redux'
import { debounce, TOAST_LOADING, TOAST_SUCCESS, TOAST_UPDATE, generateRandomArray, parseAUTHJson, shopifyApp, GET_LOCAL, SET_LOCAL, SWAL_FIRE, formatTemplateText, TOAST_ERROR } from '../../../../utils'
import { AddMagentoModal } from './Components/AddMagentoModal'
import { Link } from 'react-router-dom'
import { Loader } from '../CommonComponents/Loader'
import withRouter from '../../../routes/WithRouter'
import SearchNotFound from '../CommonComponents/NotFound/SearchNotFound'
import { checkPermission } from '../../../../actions/collaboratorActions'
import SubHeaderCrumbs from '../CommonComponents/SubHeader'
import { COMPANY_NAME } from '../../../../actions/api'
import SearchDropdown from './SearchDropdown';
import "./product.scss"

const ItemCell = (props) => <th className="fw-bold text-nowrap" style={{ minWidth: props.width }}>{props.children}</th>
class ProductCatalog extends Component {
    state = { productsyncmodal: true, currentPage: 1, showPost: 10, showAddProduct: false, searchText: '', paginationItems: [], editProductData: null, selectedProducts: [], manualSyncModal: false, uploadCsv: false, productSync: false, productName: '', addMagento: false, loading: true, m_url: '', searchLoding: false, searching: false, searchType: null }
    pageContent = window.APP_CONTENT['product_catalog'] || {}

    componentDidMount() {
        this.props.shopifyProducts()
        this.fetchAllProducts();
        window.addEventListener("message", this.handleMessage);

        //for woocommerce
        var url = new URL(window.location.href);
        var success = url.searchParams.get("success");
        if (success) {
            TOAST_SUCCESS(this.pageContent?.success_msg ?? "")
            this.props.navigate('/content/products')
        }
    }

    fetchAllProducts = () => {
        const { currentPage, showPost, searchText, searchType } = this.state
        let searchTags = "";
        const query = searchText?.length > 1 ? searchText : "";
        if(searchText) {
            searchTags = searchType?.value ?? "";
            if(searchTags?.length == 0) return TOAST_ERROR("Please select search type from dropdown");
        }
        this.props.fetchProductList(currentPage, showPost, '', query, this.generatePagination, searchTags)
    }

    handleMessage = (event) => {
        try {
            const response = parseAUTHJson(event.data);
            const { code, type } = response;
            if (type == 'shopifyLogin') if (code) {
                SHOPIFY_PRODUCT_SYNC(code, this.refreshProducts)
                setTimeout(() => {
                    this.setState({ productSync: false, manualSyncModal: false, productName: '' }, () => this.props.shopifyProducts())
                }, 200);
            }
        } catch (error) {
            console.log("--- handleMessage error ---", error);
        }
    };

    changePage = (x) => this.setState({ currentPage: x, loading: true }, this.fetchAllProducts)
    searchProduct = debounce(() => {
        const { currentPage, searchType } = this.state
        const isSearch = searchType?.value?.length > 0;
        this.setState({ loading: isSearch, searchLoding: isSearch, currentPage: isSearch ? 1 : currentPage }, this.fetchAllProducts)
    }, 800)

    generatePagination = () => {
        const { currentPage } = this.state
        const { total_pages } = this.props
        const maxPagesShown = 10;
        let pagination = [];

        if (total_pages <= maxPagesShown) {
            pagination = generateRandomArray(total_pages)
        }
        else {
            if (currentPage <= maxPagesShown / 2) {
                pagination = generateRandomArray(maxPagesShown)
            } else if (currentPage > total_pages - maxPagesShown / 2) {
                pagination = Array.from({ length: maxPagesShown }, (_, i) => total_pages - maxPagesShown + i + 1);
            } else {
                const start = Math.floor(currentPage - maxPagesShown / 2);
                pagination = Array.from({ length: maxPagesShown }, (_, i) => start + i);
            }
        }
        if (pagination[0] !== 1) {
            pagination.unshift(1);
            if (pagination[1] !== 2) {
                pagination.splice(1, 0, '...');
            }
        }
        if (pagination[pagination.length - 1] !== total_pages) {
            if (pagination[pagination.length - 1] !== total_pages - 1) {
                pagination.push('...');
            }
            pagination.push(total_pages);
        }
        this.setState({ loading: false, paginationItems: pagination, searchLoding: false })
    }

    onEditPost = (data) => {
        if (checkPermission('product_catalog', '', true)) this.setState({ editProductData: data, showAddProduct: true })
    }

    onProductSelect = (id, isChecked) => {
        const { selectedProducts } = this.state
        if (isChecked) this.setState({ selectedProducts: [...selectedProducts, id] })
        else this.setState({ selectedProducts: selectedProducts.filter(p => p != id) })
    }

    onAllProductSelect = (isChecked) => {
        if (isChecked) this.setState({ selectedProducts: this.props.allProducts.map(p => p.id) })
        else this.setState({ selectedProducts: [] })
    }

    deleteAllProduct = (product) => { 
        const { ACTION_PRODUCT_DELETE } = this.props
        const { selectedProducts, paginationItems } = this.state
        if (checkPermission('product_catalog', '', true)) {
            SWAL_FIRE({
                title: this.pageContent?.are_you_sure ?? "",
                text: formatTemplateText(this.pageContent?.are_you_sure_msg ?? "", { count: this.state.selectedProducts.length }),
                icon: 'warning',
                showCancelButton: true,
                confirmButtonText: this.pageContent?.delete_pro ?? "",
                cancelButtonText: this.pageContent?.cancel ?? "",
                confirmButtonAriaLabel: "delete_yes",
                cancelButtonAriaLabel: "delete_no",
            }).then((result) => {
                if (result.value) {
                    const toastId = TOAST_LOADING()
                    ACTION_PRODUCT_DELETE({ id: product ? [product.id] : selectedProducts }, () => {
                        if (paginationItems.length > 1) this.setState({ loading: true }, this.refreshProducts)
                        TOAST_UPDATE(toastId, "success", this.pageContent?.success_deleted ?? "")
                    })
                    this.onAllProductSelect()
                }
                else if (result.dismiss === "cancel") TOAST_SUCCESS(formatTemplateText(this.pageContent?.safe_deleted ?? "", { count: this.state?.selectedProducts?.length || 0 }))
            })
        }
    }

    checkTour = async () => {
        const tourStep = GET_LOCAL('tourStep');
        const isTourComplete = GET_LOCAL('isTour') ?? true;
        const { default: appTour } = await import('../../../../actions/appTour')
        const { tour } = new appTour(this.props.navigate, false, this.props.dispatch);
        if (!isTourComplete && tourStep?.id == '#upload-products') {
            SET_LOCAL('tourStep', { id: "#tag_pro", step: 7 });
            setTimeout(() => tour.drive(tourStep?.step ?? 6), 400);
        }
    }

    refreshProducts = () => {
        this.setState({ currentPage: 1, showPost: 10, productSync: false, searchText: '', searchType: null }, () => {
            this.fetchAllProducts()
            this.props.shopifyProducts()
        })
    }

    showManualSyncModal = (type) => {
        if(checkPermission('product_catalog', '', true)) this.setState({ [type]: true })
    }

    onDelete = (id) => {
        if (checkPermission('product_catalog', '', true)) {
            SWAL_FIRE({
                icon: "warning",
                title: this.pageContent?.are_you_sure ?? "",
                html: formatTemplateText(this.pageContent?.delete_pro_catalog ?? "", { COMPANY_NAME }),
                showCancelButton: true,
                cancelButtonText: this.pageContent?.cancel ?? "",
                confirmButtonText: this.pageContent?.delete_pro ?? "",
            }).then((result) => {
                if (result.value) this.props.STORE_DELETE(id, this.refreshProducts)
                else if (result.dismiss === "cancel") TOAST_SUCCESS(this.pageContent?.safe_delete ?? "")
            });
        }
    }

    componentWillUnmount() {
        window.removeEventListener("message", this.handleMessage);
    }

    render() {
        const { currentPage, addMagento, showAddProduct, searchText, paginationItems, editProductData, selectedProducts, manualSyncModal, uploadCsv, productSync, productName, loading, m_url, searchLoding, searching, searchType, showPost } = this.state
        const { allProducts, stores, loadingProd_cat } = this.props;
        const isShopify = shopifyApp();
        const showProducts = isShopify ? (allProducts?.length || searching) : (stores?.length || allProducts?.length || searching);
        const breadCrumbs = window?.APP_CONTENT?.['sideBar'];
        const content = this.pageContent;
        return <>
            <SubHeaderCrumbs crumbs={[{ name: breadCrumbs?.productCatalog, link: "/content/products", icon: "bag-shopping" }, { name: breadCrumbs?.home, link: "/home" }, { name: breadCrumbs?.productCatalog, link: "/content/products" }]} crumbIcon='bag-shopping' />
            <div className='d-flex flex-column flex-column-fluid w-100 pb-5 products_'>
                <Container fluid className={(!showProducts) ? "f-center h-100vh" : "container-catalogue"} style={{marginTop: '55px'}}>
                    {/* <Container fluid style={{paddingTop: '70px'}}> */}
                    {showProducts ?
                        <Card className={`border-none rounded-0 w-100`}>
                            <Card.Header className='f-between pt-lg-6 pt-4'>
                                <h4 className="text-xl">{content?.heading??"Product Catalog List"}</h4>
                                <div className="d-flex justify-content-end flex-wrap flex-sm-row flex-column flex-grow-1">
                                    <Button id="import_pro" variant='dark' size="sm" className='me-sm-2 mb-2 text-nowrap' onClick={event => this.showManualSyncModal('uploadCsv')}>
                                        <FontAwesomeIcon type={'solid'} icon={'download'} />
                                        {content?.btn_import??"Import product list"}
                                    </Button>
                                    <Button variant='primary' size="sm" className="me-sm-2 mb-2 text-nowrap" onClick={event => this.showManualSyncModal('productSync')}>{content?.btn_sync??"Sync product catalog"}</Button>
                                    <Button id="add_pro" variant='primary' size="sm" className="mb-2 text-nowrap" onClick={event => this.showManualSyncModal('showAddProduct')}>
                                        <FontAwesomeIcon type={'solid'} icon={'plus'} />
                                        {content?.btn_add??"Add product"}
                                    </Button>
                                </div>
                            </Card.Header>
                            <Card.Body className='py-0'>
                                <div className="d-flex align-items-start justify-content-between flex-wrap pt-3">
                                    <div className="f-between w-100">
                                        <div className="d-flex align-items-center gap-2 mb-2">
                                            <SearchDropdown selected={searchType} onSelect={(type) => this.setState({ searchType: type }, () => {
                                                if(searchText?.length > 0) this.fetchAllProducts()
                                            })} content={content} />
                                            <div className="p-0 position-relative w-100 d-flex flex-grow-1" style={{ maxWidth: 400 }}>
                                                <span className={`p-0 w-100 ${searchLoding ? 'spinner spinner-gray spinner-sm' : ''}`}>
                                                    <Form.Control id="search_pro" type='text' className='pe-8' value={searchText} onChange={(event) => this.setState({ searchText: event.target.value, searching: true }, this.searchProduct)} placeholder={content?.placeholder_search ?? "Search by product name, SKU, or URL"} />
                                                    {!searchLoding ?
                                                        <span id="search_spinner" className="h-38px w-38px text-gray-500 f-center position-absolute top-50 translate-middle fs-6" style={{ right: -19 }} onClick={() => searchText ? this.setState({ searchText: '' }, this.searchProduct) : []}>
                                                            <FontAwesomeIcon icon={searchText ? "xmark" : 'magnifying-glass'} />
                                                        </span> :
                                                        null
                                                    }
                                                </span>
                                            </div>
                                        </div>
                                        <div className="f-center ms-3">
                                            {selectedProducts && allProducts && (selectedProducts.length > 1 || selectedProducts.length == allProducts.length) && allProducts.length != 0 ? <Button id="d_selected_btn" variant='primary' size="sm" className="mb-2 me-4" onClick={() => this.deleteAllProduct(false)}>
                                                <FontAwesomeIcon icon={'trash-can'} />
                                                {selectedProducts.length == allProducts.length ? content?.delete_all ?? '' : formatTemplateText(content?.delete_selected ?? '', { count: `(${selectedProducts?.length || 0})` })}
                                            </Button> : null}
                                            <Link variant="secondary" id="tag_pro" to={`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`} className="btn btn-primary btn-sm me-sm-2 mb-2 text-nowrap" title='Tag products'>
                                                <FontAwesomeIcon type={'solid'} icon={'tag'} />
                                                {content?.btn_tag ?? "Tag products"}
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                                <Table className='mb-0 pb-3' size='lg' responsive>
                                    <thead className="thead-light">
                                        <tr>
                                            <th className="fw-bold flex items-center">
                                                <div className="form-check">
                                                    <Form.Check id="check_all_pro" aria-label='checkall' checked={selectedProducts.length != 0 && selectedProducts.length == allProducts.length} onChange={(e) => this.onAllProductSelect(e.target.checked)} />
                                                </div>
                                            </th>
                                            <ItemCell width={200}>{content?.table_text?.product ?? "Product"}</ItemCell>
                                            <ItemCell width={50}>{content?.table_text?.price ?? "Price"}</ItemCell>
                                            <ItemCell width={50}>{content?.table_text?.discounted_price ?? "Discounted Price"}<OverlayTrigger placement='top' overlay={<Tooltip>{content?.table_text?.tooltip_discount ?? "This represents the price after subtracting the discount."}</Tooltip>}>
                                                <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-5 ms-1 text-gray-500'} /></span>
                                            </OverlayTrigger></ItemCell>
                                            <ItemCell width={200}>{content?.table_text?.product_url ?? "Product URL"}</ItemCell>
                                            <ItemCell width={50}>{content?.table_text?.product_id ?? "Product Id"}</ItemCell>
                                            <ItemCell width={50}>{content?.table_text?.category ?? "Category"}</ItemCell>
                                            <ItemCell width={50}>{content?.table_text?.sku ?? "SKU"}</ItemCell>
                                            <ItemCell width={50}>{content?.table_text?.tags ?? "Tags"}</ItemCell>
                                            <th className="fw-bold text-nowrap text-end position-sticky end-0 pe-4" style={{ minWidth: 100 }}>{content?.table_text?.actions ?? "Actions"}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {allProducts?.length > 0 && !loading ? allProducts.map((item, index) => <Productlist key={index} product={item} selectedProducts={selectedProducts} onProductSelect={this.onProductSelect} onEditPost={this.onEditPost} onDelete={this.deleteAllProduct} content={content} />) :
                                            <tr>
                                                <td className='border-0' colSpan="10" style={{ textAlign: 'center' }}>
                                                    {loading ? <Loader bg="body" /> : <SearchNotFound title={content?.no_product ?? ""} desc={content?.no_product_desc ?? ""} icon="product" height={'calc(100vh - 400px)'} />}

                                                </td>
                                            </tr>
                                        }
                                    </tbody>
                                </Table>

                                {allProducts?.length > 0 && paginationItems?.length > 1 && !loading ?
                                    <ul className="pagination pagination-sm mb-10 mt-4 flex-wrap">
                                            <li className="page-item mt-1">
                                                <span className={`page-link cursor-pointer ${currentPage == 1 ? ' disabled' : ' cursor-pointer'}`} onClick={() => this.changePage(currentPage - 1)}>
                                                    <FontAwesomeIcon icon={'angle-left'} />
                                                </span>
                                            </li>
                                            {paginationItems.map((x, i) => <li key={i} id={`p_num_${x}`} className={`page-item${currentPage == x ? " active" : ' cursor-pointer'} ${x == '...' ? ' disabled' : ''} mt-1`} onClick={x == '...' ? null : () => this.changePage(x)}><span className="page-link">{x}</span></li>)}

                                            <li className="page-item mt-1">
                                                <span className={`page-link cursor-pointer ${currentPage == this.props.total_pages ? ' disabled' : ' cursor-pointer'}`} onClick={() => this.changePage(currentPage + 1)}>
                                                    <FontAwesomeIcon icon={'angle-right'} />
                                                </span>
                                            </li>
                                            <li className='page-item mt-1'>
                                                <select className='p-1 page-link' value={showPost} onChange={(e) => this.setState({ showPost: e.target.value, currentPage: 1, loading: true }, this.fetchAllProducts)}>
                                                    <option value="10">10</option>
                                                    <option value="20">20</option>
                                                    <option value="50">50</option>
                                                    <option value="100">100</option>
                                                </select>
                                            </li>
                                        </ul> : null}
                            </Card.Body>
                        </Card>
                        :
                        loadingProd_cat && !loading ?
                            isShopify ? <SyncProductModal isShopify={true} show={productSync} refreshProducts={this.refreshProducts} onDelete={this.onDelete} onHide={() => this.setState({ productSync: false, productName: '' })} product={productName} stores={stores} addMagento={addMagento} showMagento={(url) => this.setState({ addMagento: true, productSync: false, m_url: url })} showManualSyncModal={this.showManualSyncModal} content={content?.pop_sync} /> : <SyncProducts openManualSyncModal={() => this.setState({ manualSyncModal: true })} openSyncProduct={value => this.setState({ productName: value, productSync: true })} /> : null
                    }
                </Container>

                {productSync ? <SyncProductModal show={productSync} refreshProducts={this.refreshProducts} onDelete={this.onDelete} onHide={() => this.setState({ productSync: false, productName: '' })} product={productName} stores={stores} addMagento={addMagento} showMagento={(url) => this.setState({ addMagento: true, productSync: false, m_url: url })} showManualSyncModal={this.showManualSyncModal} content={content?.pop_sync} /> : null}

                <ImportProducts show={uploadCsv} onHide={() => this.setState({ uploadCsv: false })} countAllPage={this.refreshProducts} content={content?.pop_import} />

                {showAddProduct ? <AddProductModal showAddProduct={showAddProduct} editProductData={editProductData} ACTION_PRODUCT_UPDATE={this.props.ACTION_PRODUCT_UPDATE} closePopup={() => this.setState({ showAddProduct: false, editProductData: null })} ACTION_PRODUCT_ADD={this.props.ACTION_PRODUCT_ADD} content={content?.pop_add} /> : null}

                <ManualUploadModal show={manualSyncModal} handleClose={() => this.setState({ manualSyncModal: false })} uploadCsv={() => this.setState({ uploadCsv: true, manualSyncModal: false })} onAddProduct={() => this.setState({ showAddProduct: true, manualSyncModal: false })} />
                {addMagento && <AddMagentoModal show={addMagento} onHide={() => this.setState({ addMagento: false })} refreshProducts={this.refreshProducts} m_url={m_url} />}

            </div>
        </>
    }
}



const mapStateToProps = state => {
    return {
        allProducts: state.products?.allProducts,
        total_pages: state.products?.total_pages,
        loadingProd_cat: state.products?.loadingProd_cat,
        stores: state.products?.connectedStores,
        data: state.products.loadingProd_cat,
        dataNew: state.products,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        fetchProductList: (page, showPost, tag, search, callBack, searchType) => dispatch(fetchProductList(page, showPost, tag, search, callBack, null, null, searchType)),
        ACTION_PRODUCT_ADD: (obj, callBack) => dispatch(ACTION_PRODUCT_ADD(obj, callBack)),
        ACTION_PRODUCT_UPDATE: (obj, callBack) => dispatch(ACTION_PRODUCT_UPDATE(obj, callBack)),
        ACTION_PRODUCT_DELETE: (obj, callBack) => dispatch(ACTION_PRODUCT_DELETE(obj, callBack)),
        shopifyProducts: () => dispatch(shopifyProducts()),
        STORE_DELETE: (id, callback) => dispatch(STORE_DELETE(id, callback))
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(withRouter(React.memo(ProductCatalog)))