import React, { Component } from 'react'
import { Modal, Button, Image, Form, Alert } from 'react-bootstrap';
import { CONNECT_SHOPIFY_STORE, GUEST_WOOCOMMERCE_ADD, MAGENTO_PRODUCT_SYNC, NODE_GMC_LOGIN, SHOPIFY_PRODUCT_SYNC, STORE_DELETE, SYNC_BIGCOMMERCE_PRODUCTS, SYNC_GMC_PRODUCT, SYNC_WOOCOMMERCE_PRODUCTS } from '../../../../../actions/productAction';
import { getGoogleCode, GMC_AUTH } from '../../../../../utils/auth';
import { formatTemplateText, localTimeConvert, parseAUTHJson, TOAST_ERROR, TOAST_SUCCESS, URL_VALIDATION } from '../../../../../utils';
import { Link } from 'react-router-dom';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { GET_GMC_AUTH } from '../../../../../actions/socialUrl';
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC';

export default class SyncProductModal extends Component {
    state = { shopifyUrl: '', gmc_id: '', m_url: '', w_url: '', gmc_error: false, shopify_error: false, m_error: false, w_error: false, isConnected:false }

    appContent = window?.APP_CONTENT ?? {};

    handleMessage = async (event) => {
        try {
            const response = parseAUTHJson(event.data);
            const { code, type } = response;
                if (code) {
                    if(type == 'shopifyLogin'){
                        SHOPIFY_PRODUCT_SYNC(code, this.refreshProducts)
                        setTimeout(() => {
                            this.props.refreshProducts();
                        }, 200);
                    }
                    else{
                        try {
                            const data = await GET_GMC_AUTH(code);
                            if (data) {
                                const res = await NODE_GMC_LOGIN(data);
                                localStorage.removeItem("shopUrl");
                                if (res.error) TOAST_ERROR(this.appContent?.common?.some_error_occured ?? "");
                                else {
                                    this.props.refreshProducts();
                                    TOAST_SUCCESS(res.message);
                                }
                            }
                        } catch (error) {
                            console.error('Error during authentication:', error);
                            TOAST_ERROR(this.appContent?.common?.auth_failed ?? "");
                        }
                    }
            }
        } catch (error) {
            console.error("Error parsing event data:", error);
        }
    };

    componentDidMount() {
        window.addEventListener("message", this.handleMessage);
    }

    onAddGmcAccount = (id) => {
        const isValid = /^\d{1,18}$/.test(id);
        this.setState({ gmc_error: !isValid });
        if(isValid){
            GMC_AUTH();
            localStorage.setItem("shopUrl", id);
        }
    }

    setUrl = (url, key, errorKey) => {
        const isValid = URL_VALIDATION(url);
        let temp = {};
        if(key == "shopifyUrl") temp["isConnected"] = false;
        this.setState({ [key]: url, [errorKey]: url == '' ? false : !isValid, ...temp })
    }

    checkShopify = (shopifyUrl) => {
        if(String(shopifyUrl).trim() == '') this.setState({shopify_error:true})
        else{
            const { stores } = this.props
            let present = []
            if(stores?.length > 0) present = stores.filter(store=> String(store.domain).toLowerCase() == String(shopifyUrl).toLowerCase())
            if(present.length > 0) this.setState({isConnected:true, shopify_error:true})
            else CONNECT_SHOPIFY_STORE(String(shopifyUrl).toLowerCase())
        }
    }

    componentWillUnmount() {
        window.removeEventListener("message", this.handleMessage);
    }

    render() {
        const { show, onHide, product, stores, showMagento, onDelete, refreshProducts, isShopify, showManualSyncModal } = this.props
        const { gmc_error, shopify_error, m_error, w_error, isConnected, shopifyUrl, gmc_id, m_url, w_url } = this.state;
        const shopifyStore = isShopify ? stores?.find(store => store.created_from == 'shopify') : null;
        const content = this.appContent?.product_catalog?.pop_sync ?? "";
        
        return (isShopify && !show) ? <div className="shopifyApp-modal card shadow-sm border-0 rounded-3 w-100">
                <div className="card-body">
                    <div className="card-top">
                        <div className="text-center mb-5 pb-3">
                            <h4 className="fs-4 fw-bold">{content?.heading_shopify ?? ""}</h4>
                        </div>
                        <div className="d-flex align-items-start flex-md-row flex-column">
                            <div className="flex-md-grow-1 flex-grow-0 me-2 w-100 mb-5 mb-md-0">
                                <Form.Control id="shopify_c_input" size='lg' placeholder='example.myshopify.com' value={shopifyStore?.domain ?? ""} readOnly />
                                {shopifyStore?.modified ? <span className="text-gray-700 fs-8 fw-medium">{formatTemplateText(content?.last_synced ?? "", {time: localTimeConvert(shopifyStore?.modified ?? "")})}</span> : null}
                            </div>
                            <Button id="shopify_id_btn" className="btn border-0 d-flex align-items-center signInWithBrand signIn-Shopify" value="shopify" onClick={() => SHOPIFY_PRODUCT_SYNC(shopifyStore?.id, refreshProducts)}>
                                <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                    <img src={baseUrl("media/icons/platform-icons/shopify.svg")} height={20} width={20} alt="shopify" />
                                </span>
                                <div className="overflow-hidden signIn_txt">
                                    <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.connect_shop ?? ""}</span>
                                </div>
                            </Button>
                        </div>
                    </div>
                    <div className="card-bottom px-4">  
                        <div className="text-center mb-5">
                            <p className="text-muted mb-0">{content?.sync_product_other ?? ""}</p>
                        </div>
                        <div className="d-flex justify-content-center flex-column flex-sm-row gap-3 mb-4">
                            <Button variant="secondary" className="d-flex align-items-center justify-content-center" onClick={() => showManualSyncModal('showAddProduct')}>
                                <FontAwesomeIcon icon={'plus'} className="me-2" />
                                {content?.add_product ?? ""}
                            </Button>
                            <Button variant="secondary" className="d-flex align-items-center justify-content-center" onClick={() => showManualSyncModal('uploadCsv')}>
                                <FontAwesomeIcon icon={'file-import'} className="me-2" />
                                {content?.import ?? ""}
                            </Button>
                        </div>
                    </div>
                </div>
            </div> : <Modal show={show} onHide={onHide} size="xl" id="syncProducts" aria-labelledby="syncProducts" centered>
                <Modal.Header closeButton>
                    <Modal.Title>{content?.heading ?? 'Sync Product'}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="p-lg-8 p-4">
                    {!localStorage.getItem('shopifyLogin') ? 
                    <div>
                        {['google', ''].includes(product) ? <div className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                            <div className="d-flex flex-shrink-0">
                                <div className="d-flex align-items-center mb-2 py-1">
                                    <span className="symbol symbol-30 me-2">
                                        <Image className="h-100" src={baseUrl("media/icons/platform-icons/google.svg")} height={43} width={43} alt="Google Merchant" fluid />
                                    </span>
                                    <span className="fw-semibold fs-8 text-ellipsis">{content?.google_merchant ?? 'Google Merchant'}</span>
                                </div>
                            </div>
                            <div className="d-flex flex-grow-1">
                                <div className="mb-2 w-100 mx-0 mx-sm-3">
                                    <Form.Control id="google_id_input" size='lg' className={gmc_error ? 'is-invalid' : ''} placeholder={content?.plc_google ?? 'Google Merchant ID'} value={gmc_id} onInput={e => this.setState({ gmc_id: e.target.value, gmc_error: false })} required />
                                    {gmc_error ? <div className="invalid-feedback">{gmc_id ? content?.err_google_invalid ?? "" : content?.err_required ?? ""}</div> : null}
                                </div>
                            </div>
                            <div className="d-flex flex-shrink-0">
                                <Button id="google_id_btn" className="btn border-0 d-flex signInWithBrand signIn-Google mb-2" onClick={() => { if (gmc_id) this.onAddGmcAccount(gmc_id); else this.setState({ gmc_error: true }) }}>
                                    <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                        <img src={baseUrl("media/icons/platform-icons/google.svg")} height={43} width={43} alt="google" />
                                    </span>
                                    <div className="overflow-hidden signIn_txt">
                                        <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.btn_google ?? "Sign in with Google"}</span>
                                    </div>
                                </Button>
                            </div>
                        </div>
                            : null}
                                {['shopify', ''].includes(product) ? <div className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                    <div className="d-flex flex-shrink-0">
                                        <div className="d-flex align-items-center mb-2 py-1">
                                            <span className="symbol symbol-30 me-2">
                                                <Image className="h-100" src={baseUrl("media/icons/platform-icons/shopify.svg")} height={20} width={20} alt="shopify" fluid />
                                            </span>
                                            <span className="fw-semibold fs-8 text-ellipsis">{content?.shopify ?? "Shopify"}</span>
                                        </div>
                                    </div>
                                    <div className="d-flex flex-grow-1">
                                        <div className="mb-2 w-100 mx-0 mx-sm-3">
                                            <Form.Control id="shopify_id_input" size='lg' className={shopify_error ? 'is-invalid' : ''} placeholder={content?.plc_shopify ?? 'example.myshopify.com'} value={shopifyUrl} onInput={(e) => this.setUrl(e.target.value, 'shopifyUrl', 'shopify_error')} required />
                                            {shopify_error ? <div className="invalid-feedback">{isConnected ? content?.err_shopify_connected ?? "" : content?.err_shopify_invalid ?? ""}</div> : null}
                                        </div>
                                    </div>
                                    <div className="d-flex flex-shrink-0">
                                        <Button id="shopify_id_btn" className="btn border-0 d-flex signInWithBrand signIn-Shopify mb-2" value="shopify" onClick={(shopifyUrl && !shopify_error) ? () => this.checkShopify(shopifyUrl) : () => { this.setState({ shopify_error: true }) }}>
                                            <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                                <img src={baseUrl("media/icons/platform-icons/shopify.svg")} height={20} width={20} alt="shopify" />
                                            </span>
                                            <div className="overflow-hidden signIn_txt">
                                                <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.btn_shopify ?? "Connect Shop"}</span>
                                            </div>
                                        </Button>
                                    </div>
                                </div> : null}

                        {['bmc', ''].includes(product) ? <div className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                            <div className="d-flex flex-shrink-0">
                                <div className="d-flex align-items-center mb-2 py-1">
                                    <span className="symbol symbol-30 me-2">
                                        <Image className="h-100 invert" src={baseUrl("media/icons/platform-icons/bigcommerce.svg")} height={20} width={20} alt="bigcommerce" title="bigcommerce" fluid />
                                    </span>
                                    <span className="fw-semibold fs-8 text-ellipsis">{content?.bigcommerce ?? "BigCommerce"}</span>
                                </div>
                            </div>
                            <div className="d-flex flex-grow-1">
                                <div className="my-2 w-100 mx-0 mx-sm-3">
                                    <p className="mb-0 fs-8">{content?.plc_bigcommerce ?? 'Install our BigCommerce app to sync products.'}</p>
                                </div>
                            </div>
                            <div className="d-flex flex-shrink-0">
                                <Link id="bmc_btn" to="https://www.bigcommerce.com/apps/taggshop" className="border-0 d-flex signInWithBrand signIn-Bigcommerce mb-2" title='CSV'>
                                    <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                        <Image src={baseUrl("media/icons/platform-icons/bigcommerce.svg")} height={20} width={20} alt="bigcommerce" title="bigcommerce" />
                                    </span>
                                    <div className="overflow-hidden signIn_txt">
                                        <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.btn_bigcommerce ?? 'Install App'}</span>
                                    </div>
                                </Link>
                            </div>
                        </div> : null}

                        {['magento', ''].includes(product) ? <div className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                            <div className="d-flex flex-shrink-0">
                                <div className="d-flex align-items-center mb-2 py-1">
                                    <span className="symbol symbol-30 me-2">
                                        <Image className="h-100" src={baseUrl("media/icons/platform-icons/magento.svg")} height={20} width={20} alt="magento" title="magento" fluid/>
                                    </span>
                                    <span className="fw-semibold fs-8 text-ellipsis">{content?.magento ?? "Magento"}</span>
                                </div>
                            </div>
                            <div className="d-flex flex-grow-1 flex-column mx-0 mx-sm-3">
                                <div className="mb-2 w-100">
                                    <Form.Control id="magento_input" size='lg' className={m_error ? 'is-invalid' : ''} placeholder={content?.plc_magento ?? 'Enter your "magento" store URL'} value={m_url} onInput={e => this.setUrl(e.target.value, 'm_url', 'm_error')} required />
                                    {m_error ? <div className="invalid-feedback">{content?.err_required ?? ""}</div> : null}
                                </div>
                                <span className="text-gray-700 fs-8 fw-medium">{content?.text_magento ?? "Note: Only Compatible with Magento 2.x"}</span>
                            </div>
                            <div className="d-flex flex-shrink-0">
                                <Button id="magento_btn" className="border-0 d-flex signInWithBrand signIn-Magento mb-2" onClick={m_url && !m_error ? () => { showMagento(m_url);}:()=>{this.setState({m_error:true})}}>
                                    <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                        <img src={baseUrl("media/icons/platform-icons/magento.svg")} height={20} width={20} alt="magento" />
                                    </span>
                                    <div className="overflow-hidden signIn_txt">
                                        <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.btn_shopify ?? "Connect Shop"}</span>
                                    </div>
                                </Button>
                            </div>
                        </div> : null}

                        {['woocommerce', ''].includes(product) ? <div className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                            <div className="d-flex flex-shrink-0">
                                <div className="d-flex align-items-center mb-2 py-1">
                                    <span className="symbol symbol-30 me-2">
                                        <img className="img-fluid h-100" src={baseUrl("media/icons/platform-icons/woocommerce.svg")} height={20} width={20} alt="woocommerce" />
                                    </span>
                                    <span className="fw-semibold fs-8 text-ellipsis">{content?.woocommerce ?? "Woocommerce"}</span>
                                </div>
                            </div>
                            <div className="d-flex flex-grow-1 flex-column mx-0 mx-sm-3">
                                <div className="mb-2 w-100">
                                    <Form.Control id="wooco_input" size='lg' className={w_error ? 'is-invalid' : ''} placeholder={content?.plc_woocommerce ?? 'Enter your "woocommerce" store URL'} value={w_url} onInput={(e) => this.setUrl(e.target.value, 'w_url', 'w_error')} required />
                                    {w_error ? <div className="invalid-feedback">{content?.err_required ?? ""}</div> : null}
                                </div>
                            </div>
                            <div className="d-flex flex-shrink-0">
                                <Button id="wooco_btn" className="border-0 d-flex signInWithBrand signIn-Woocommerce mb-2" onClick={() => {w_url && !w_error ? GUEST_WOOCOMMERCE_ADD(w_url) : this.setState({w_error:true})}}>
                                    <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                        <img src={baseUrl("media/icons/platform-icons/woocommerce.svg")} height={20} width={20} alt="woocommerce" />
                                    </span>
                                    <div className="overflow-hidden signIn_txt">
                                        <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.btn_shopify ?? "Connect Shop"}</span>
                                    </div>
                                </Button>
                            </div>
                        </div> : null}
                    </div> : null}
                    {stores && product == '' && stores.length ?
                        <div>
                            {!localStorage.getItem('shopifyLogin') ? <h4>{content?.connected_shop ?? ""}</h4>:''}
                            <div className={`${!localStorage.getItem('shopifyLogin') ? 'border-top ':'' }pt-5`}>
                                {stores.map(store => {
                                    if (store.created_from == 'googlemerchant') {
                                        return (<div key={0} className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                            <div className="d-flex flex-shrink-0">
                                                <div className="d-flex align-items-center mb-2 py-1">
                                                    <span className="symbol symbol-30 me-2">
                                                        <Image className="h-100" src={baseUrl("media/icons/platform-icons/google.svg")} height={43} width={43} alt="Google Merchant" fluid />
                                                    </span>
                                                    <span className="fw-semibold fs-8 text-ellipsis">{content?.google_merchant ?? "Google Merchant"}</span>
                                                </div>
                                            </div>
                                            <div className="d-flex flex-grow-1">
                                                <div className="mb-2 w-100 mx-0 mx-sm-3">
                                                    <Form.Control id="google_c_id" size='lg' placeholder={content?.plc_google ?? "Google Merchant ID"} value={store.storeId} readOnly />
                                                    {store.modified ? <span className="text-gray-700 fs-8 fw-medium">{formatTemplateText(content?.last_synced ?? "", {time: localTimeConvert(store.modified)})}</span> : ''}
                                                </div>
                                            </div>
                                            <div className="d-flex flex-shrink-0">
                                                <Button id="google_c_btn" className="btn border-0 d-flex signInWithBrand signIn-Google mb-2" onClick={() => SYNC_GMC_PRODUCT(store.id, (reconnect) => {
                                                    if (reconnect) this.onAddGmcAccount(store.storeId);
                                                    else refreshProducts();
                                                })}>
                                                    <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                                        <img src={baseUrl("media/icons/platform-icons/google.svg")} height={43} width={43} alt="Google Merchant" />
                                                    </span>
                                                    <div className="overflow-hidden signIn_txt">
                                                        <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.btn_google ?? "Sign in with Google"}</span>
                                                    </div>
                                                </Button>
                                                <div className='ms-2'>
                                                    <Button id="google_c_d_btn" variant='icon' size='lg' className="btn-active-danger" aria-label="delete" onClick={() => onDelete(store.id)}>
                                                        <FontAwesomeIcon type="solid" icon={'trash-can'} />
                                                    </Button>
                                                </div>
                                            </div>
                                        </div>
                                        )
                                    }
                                    if (store.created_from == 'bigcommerce') {
                                        return (<div key={1} className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                            <div className="d-flex flex-shrink-0">
                                                <div className="d-flex align-items-center mb-2 py-1">
                                                    <span className="symbol symbol-30 me-2">
                                                        <Image className="h-100 invert" src={baseUrl("media/icons/platform-icons/bigcommerce.svg")} height={20} width={20} alt="bigcommerce" fluid />
                                                    </span>
                                                    <span className="fw-semibold fs-8 text-ellipsis">{content?.bigcommerce ?? "BigCommerce"}</span>
                                                </div>
                                            </div>
                                            <div className="d-flex flex-grow-1 flex-column mx-0 mx-sm-3">
                                                <div className="mb-2 w-100">
                                                    <Form.Control id="bcm_c_input" size='lg' placeholder='Enter your "magento" store URL' value={store.domain} readOnly />
                                                </div>
                                                {store.modified ? <span className="text-gray-700 fs-8 fw-medium">{formatTemplateText(content?.last_synced ?? "", {time: localTimeConvert(store.modified)})}</span> : null}
                                            </div>
                                            <div className="d-flex flex-shrink-0">
                                                <Button id="bcm_c_btn" className="border-0 d-flex signInWithBrand signIn-Bigcommerce mb-2" onClick={() => SYNC_BIGCOMMERCE_PRODUCTS(store.id, refreshProducts)}>
                                                    <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                                        <img src={baseUrl("media/icons/platform-icons/bigcommerce.svg")} height={20} width={20} alt="bigcommerce" />
                                                    </span>
                                                    <div className="overflow-hidden signIn_txt">
                                                        <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.sync_products??"Sync Products"}</span>
                                                    </div>
                                                </Button>
                                            </div>
                                        </div>
                                        )
                                    }
                                    if (store.created_from == 'shopify') {
                                        return (<div key={2} className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                            <div className="d-flex flex-shrink-0">
                                                <div className="d-flex align-items-center mb-2 py-1">
                                                    <span className="symbol symbol-30 me-2">
                                                        <Image className="h-100" src={baseUrl("media/icons/platform-icons/shopify.svg")} height={20} width={20} alt="shopify" fluid />
                                                    </span>
                                                    <span className="fw-semibold fs-8 text-ellipsis">{content?.shopify ?? "Shopify"}</span>
                                                </div>
                                            </div>
                                            <div className="d-flex flex-grow-1">
                                                <div className="mb-2 w-100 mx-0 mx-sm-3">
                                                    <Form.Control id="shopify_c_input" size='lg' placeholder='example.myshopify.com' value={store.domain} readOnly />
                                                    {store.modified ? <span className="text-gray-700 fs-8 fw-medium">{formatTemplateText(content?.last_synced ?? "", {time: localTimeConvert(store.modified)})}</span> : ''}
                                                </div>
                                            </div>
                                            <div className="d-flex flex-shrink-0">
                                                <Button id="shopify_c_btn" className="btn border-0 d-flex signInWithBrand signIn-Shopify mb-2" value="shopify" onClick={() => SHOPIFY_PRODUCT_SYNC(store.id, refreshProducts)}>
                                                    <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                                        <img src={baseUrl("media/icons/platform-icons/shopify.svg")} height={20} width={20} alt="shopify" />
                                                    </span>
                                                    <div className="overflow-hidden signIn_txt">
                                                        <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.btn_shopify??"Connect Shop"}</span>
                                                    </div>
                                                </Button>
                                                {!localStorage.getItem('shopifyLogin') ?
                                                <div className='ms-2'>
                                                    <Button id="shopify_c_d_btn" variant='icon' size='lg' className="btn-active-danger" aria-label="delete" onClick={() => onDelete(store.id)}>
                                                        <FontAwesomeIcon type="solid" icon={'trash-can'} />
                                                    </Button>
                                                </div> :''}
                                            </div>
                                        </div>)
                                    }
                                    if (store.created_from == 'magento') {
                                        return (<div key={3} className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                            <div className="d-flex flex-shrink-0">
                                                <div className="d-flex align-items-center mb-2 py-1">
                                                    <span className="symbol symbol-30 me-2">
                                                        <img className="img-fluid h-100" src={baseUrl("media/icons/platform-icons/magento.svg")} height={20} width={20} alt="magento" />
                                                    </span>
                                                    <span className="fw-semibold fs-8 text-ellipsis">{content?.magento??"Magento"}</span>
                                                </div>
                                            </div>
                                            <div className="d-flex flex-grow-1 flex-column mx-0 mx-sm-3">
                                                <div className="mb-2 w-100">
                                                    <Form.Control id="magento_c_url_input" className='mb-3' size='lg' placeholder={content?.plc_magento ?? 'Enter your "magento" store URL'} value={store?.domain} readOnly />
                                                    <Alert variant='success' className='p-2 rounded-1 mb-2'>
                                                        <Alert.Heading className='h5 mb-1'><span className='text-muted'><FontAwesomeIcon icon={'store'} iconClass={'me-1'} /> {content?.label_store ?? ""} </span>{store?.storeId}</Alert.Heading>
                                                        {store.modified ? <p className='mb-0'>{formatTemplateText(content?.last_synced ?? "", {time: localTimeConvert(store.modified)})}</p> : ''}
                                                    </Alert>
                                                </div>
                                                <span className="text-gray-700 fs-8 fw-medium">{content?.text_magento ??"Note: Only Compatible with Magento 2.x"}</span>
                                            </div>
                                            <div className="d-flex flex-shrink-0">
                                                <Button id="magento_c_btn" className="border-0 d-flex signInWithBrand signIn-Magento mb-2" onClick={() => MAGENTO_PRODUCT_SYNC(store?.storeId, store?.accessToken, store?.shop, (reconnect) => {
                                                    if (reconnect === true) showMagento(store?.domain);
                                                    else if(!reconnect) refreshProducts();
                                                })}>
                                                    <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                                        <img src={baseUrl("media/icons/platform-icons/magento.svg")} height={20} width={20} alt="magento" />
                                                    </span>
                                                    <div className="overflow-hidden signIn_txt">
                                                        <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.sync_products??"Sync Products"}</span>
                                                    </div>
                                                </Button>
                                                <div className='ms-2'>
                                                    <Button id="magento_c_d_btn" variant='icon' size='lg' className="btn-active-danger" aria-label="delete" onClick={() => onDelete(store.id)}>
                                                        <FontAwesomeIcon type="solid" icon={'trash-can'} />
                                                    </Button>
                                                </div>
                                            </div>
                                        </div>)
                                    }
                                    if (store.created_from == 'woocommerce') {
                                        return (<div key={4} className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                            <div className="d-flex flex-shrink-0">
                                                <div className="d-flex align-items-center mb-2 py-1">
                                                    <span className="symbol symbol-30 me-2">
                                                        <img className="img-fluid h-100" src={baseUrl("media/icons/platform-icons/woocommerce.svg")} height={20} width={20} alt="twitter" />
                                                    </span>
                                                    <span className="fw-semibold fs-8 text-ellipsis">{content?.woocommerce??"Woocommerce"}</span>
                                                </div>
                                            </div>
                                            <div className="d-flex flex-grow-1 flex-column mx-0 mx-sm-3">
                                                <div className="mb-2 w-100">
                                                    <Form.Control id="woco_c_input" size='lg' placeholder={content?.plc_woocommerce??'Enter your "woocommerce" store URL'} value={store?.domain} readOnly />
                                                    {store.modified ? <span className="text-gray-700 fs-8 fw-medium">{formatTemplateText(content?.last_synced ?? "", {time: localTimeConvert(store.modified)})}</span> : ''}
                                                </div>
                                            </div>
                                            <div className="d-flex flex-shrink-0">
                                                <Button id="woco_c_btn" className="border-0 d-flex signInWithBrand signIn-Woocommerce mb-2" onClick={() => SYNC_WOOCOMMERCE_PRODUCTS(store?.id, refreshProducts)}>
                                                    <span className="h-38px w-38px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                                        <img src={baseUrl("media/icons/platform-icons/woocommerce.svg")} height={20} width={20} alt="woocommerce" />
                                                    </span>
                                                    <div className="overflow-hidden signIn_txt">
                                                        <span className="text-white mb-0 w-100 fs-8 fw-medium text-ellipsis mx-1">{content?.sync_products??"Sync Products"}</span>
                                                    </div>
                                                </Button>
                                                <div className='ms-2'>
                                                    <Button id="woco_c_d_btn" variant='icon' size='lg' className="btn-active-danger" aria-label="delete" onClick={() => onDelete(store.id)}>
                                                        <FontAwesomeIcon type="solid" icon={'trash-can'} />
                                                    </Button>
                                                </div>
                                            </div>
                                        </div>)
                                    }

                                }
                                )}
                            </div>
                        </div>
                        : null}
                        <RequestButtonHOC title={content?.btn_req ?? 'Request Platform'}>
                            <Button variant="secondary">
                                <FontAwesomeIcon icon={'plus'}/>
                                {content?.btn_req ?? "Request Platform"}
                            </Button>
                        </RequestButtonHOC>
                        </div>
                </Modal.Body>
            </Modal>
    }
}